import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import { route } from 'preact-router';

import Dog from '../../components/dog';
import Workout from '../../components/workout';
import Team from '../../components/team';
import Track from '../../components/track';
import User from '../../components/user';
import Story from '../../components/story/publicStory';

import TeamPublicFeedLine from '../../components/team/publicFeedLine';

const initialState = {
	submenu: 'dogs',
	hits: 0,
	searchText: '',
};

@observer
class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadAll = async (props = this.props) => {
		const { appState, storyStore, teamStore } = this.props.stores;
		const { search } = props;
	    await appState.getSearch(search);
		const { publicTeams, publicUsers } = appState;
		storyStore.localUpdateField('publicTeams', publicTeams);
		storyStore.localUpdateField('publicUsers', publicUsers);
		teamStore.localUpdateField('publicTeams', publicTeams);
		teamStore.localUpdateField('publicUsers', publicUsers);

		const { searchResultStories, searchResultDogs, searchResultWorkouts = [], searchResultTeams, searchResultTracks } = appState;
		const allData = [searchResultStories, searchResultDogs, searchResultWorkouts, searchResultTeams, searchResultTracks];
		const names = ['stories', 'dogs', 'workouts', 'teams', 'tracks', 'users'];
		const idx = allData
			.map(a => a.length)
			.indexOf(Math.max(...allData.map(a => a.length)));
		const hits = allData.map(a => a.length).reduce((partialSum, a) => partialSum + a, 0);
		this.setState({
			searchText: search,
			submenu: names[idx],
			hits,
		});
	}

	search = () => {
		const { searchText } = this.state;
		route(`/search/${searchText}`);
	}

	chooseSubmenu = (e) => {
		const submenu = e.target.parentElement.dataset.menu || e.target.dataset.menu;
		this.setState({ submenu });
	}

	componentDidMount() {
		this.loadAll();
	}

	componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.search !== this.props.search) {
            this.loadAll(nextProps);
        }
    }

	render() {
		const { appState, userStore } = this.props.stores;
		const { search } = this.props;

		const { isAdmin } = userStore;
		const { searchResultStories, searchResultDogs, searchResultWorkouts = [], searchResultTeams, searchResultTracks, publicUsers, publicTeams } = appState;
		const { submenu, hits, searchText } = this.state;
		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h1><Text id='search.title'>Search</Text></h1>
						<h5 class='my-3'><Text id='search.search-for' fields={{ search, hits }}>{hits} in your search for "{search}"</Text></h5>

						<div class='d-flex justify-content-center py-3'>
							<Localizer>
								<input class='form-control form-control mr-2' type='search' placeholder={<Text id='header.search-placeholder'>Søk etter det meste...</Text>}
									value={searchText}
									onInput={linkState(this, 'searchText')}
								/>
							</Localizer>
							<button class='btn btn-outline-success' type='button'
								onClick={this.search}
							><Text id='header.search'>Søk</Text></button>
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
						<div class='row'>
							<div class='w-100 overflow-hidden'>
                                <div
                                    class='d-flex flex-row flex-nowrap pb-2 px-1'
                                    style='overflow: auto; scroll-snap-type: x mandatory;'
                                >
									<div class='col px-0 pt-2'>
										<button
											class={`btn btn-block rounded-pill-left rounded-none ${submenu === 'dogs' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='dogs'
											onClick={this.chooseSubmenu}
										><i class='fas fa-dog' /> <Text id='search.dogs'>Dogs</Text> ({searchResultDogs.length || 0})</button>
									</div>
									{/* <div class='col px-0 pt-2'>
										<button
											class={`btn btn-block rounded-none ${submenu === 'workouts' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='workouts'
											onClick={this.chooseSubmenu}
										><i class='fas fa-running' /> <Text id='search.workouts'>Workouts</Text> ({searchResultWorkouts.length || 0})</button>
									</div> */}
									<div class='col px-0 pt-2'>
										<button
											class={`btn btn-block rounded-none ${submenu === 'teams' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='teams'
											onClick={this.chooseSubmenu}
										><i class='fas fa-users' /> <Text id='search.teams'>Teams</Text> ({searchResultTeams.length || 0})</button>
									</div>
									<div class='col px-0 pt-2'>
										<button
											class={`btn btn-block rounded-none ${submenu === 'tracks' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='tracks'
											onClick={this.chooseSubmenu}
										><i class='fas fa-map-marked-alt' /> <Text id='search.tracks'>Tracks</Text> ({searchResultTracks.length || 0})</button>
									</div>
									<div class='col px-0 pt-2'>
										<button
											class={`btn btn-block rounded-pill-right rounded-none ${submenu === 'stories' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
											style='text-overflow: ellipsis; overflow: hidden;'
											data-menu='stories'
											onClick={this.chooseSubmenu}
										><i class='fas fa-comments' /> <Text id='search.stories'>Stories</Text> ({searchResultStories.length || 0})</button>
									</div>
								</div>
							</div>
						</div>
					</div>

					{submenu === 'dogs' && <>
						<div class='col-12'>
							{(!searchResultDogs || searchResultDogs.length === 0) && <>
								<div class='text-center text-muted'>
									<div class='display-1'>
										<i class='fas fa-dog' />
									</div>
									<h3 class='font-weight-lighter'>
										<Text id='search.no-dogs-found'>No dogs found.</Text>
									</h3>
								</div>
							</>}
							{searchResultDogs && searchResultDogs.map(dog => (
								<Dog stores={this.props.stores} dog={dog} url={`/dogs/public/${dog.uuidv4}`} />
							))}
						</div>
					</>}
					{submenu === 'workouts' && <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
							{(!searchResultWorkouts || searchResultWorkouts.length === 0) && <>
								<div class='text-center text-muted'>
									<div class='display-1'>
										<i class='fas fa-running' />
									</div>
									<h3 class='font-weight-lighter'>
										<Text id='search.no-workouts-found'>No workouts found.</Text>
									</h3>
								</div>
							</>}
							{searchResultWorkouts && searchResultWorkouts.map((workout, idx) => (
								<div class='row mb-4'>
									<div class='col-12 py-3'>
										<TeamPublicFeedLine
											stores={this.props.stores}
											feed={workout}
											idx={idx}
										/>
									</div>
								</div>
							))}
						</div>
					</>}
					{submenu === 'teams' && <>
						<div class='col-12'>
							{(!searchResultTeams || searchResultTeams.length === 0) && <>
								<div class='text-center text-muted'>
									<div class='display-1'>
										<i class='fas fa-users' />
									</div>
									<h3 class='font-weight-lighter'>
										<Text id='search.no-teams-found'>No teams found.</Text>
									</h3>
								</div>
							</>}
							{searchResultTeams && searchResultTeams.map(team => <>
								<Team
									stores={this.props.stores}
									team={team}
									url={`/teams/public/${team.uuidv4}`}
									showButton={true}
									publicUsers={publicUsers}
									showMembers={1}
									showLocation={1}
								/>
							</>)}
						</div>
					</>}
					{submenu === 'tracks' && <>
						<div class='col-12'>
							{(!searchResultTracks || searchResultTracks.length === 0) && <>
								<div class='text-center text-muted'>
									<div class='display-1'>
										<i class='fas fa-map-marked-alt' />
									</div>
									<h3 class='font-weight-lighter'>
										<Text id='search.no-tracks-found'>No tracks found.</Text>
									</h3>
								</div>
							</>}
							{searchResultTracks && searchResultTracks.map(track => (
								<Track stores={this.props.stores} track={track} />
							))}
						</div>
					</>}
					{submenu === 'stories' && <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
							{(!searchResultStories || searchResultStories.length === 0) && <>
								<div class='text-center text-muted'>
									<div class='display-1'>
										<i class='fas fa-comments' />
									</div>
									<h3 class='font-weight-lighter'>
										<Text id='search.no-stories-found'>No stories found.</Text>
									</h3>
								</div>
							</>}
							{searchResultStories && searchResultStories.map(story => (
								<div class='row mb-4'>
									<div class='col-12 py-3'>
										<Story
											stores={this.props.stores}
											story={story}
											key={`story-detail-${story.id}`}
										/>
									</div>
								</div>
							))}
						</div>
					</>}
				</div>
			</div>
		);
	}
}

export default Search;
