import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Modal from '../modal';
import TrackComments from '../../components/track/comments';
import TrackAddComment from '../../components/track/commentAdd';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

import mu from '../../lib/musher-util';

const MAX_LOADINDEX = mu.getMaxLoadIndex();

const initialState = {
	showAddComment: false,
};

@observer
class Track extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

    likeTrack = async () => {
		const { trackStore } = this.props.stores;
        const { track } = this.props;
		const response = await trackStore.likeTrack({ id: track.id });

        if (response.status === 200) {
			
		}
	}

	toggleAddComment = (e) => {
		const id = e.target.parentElement.dataset.id || e.target.dataset.id;
		const { showAddComment } = this.state;
		this.setState({
			showAddComment: !showAddComment,
		}, () => {
			if (this.inputRef[id]) {
				this.inputRef[id].focus();
			}
		});
	}

    showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
        const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

    toggleDate = () => {
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

    render() {
        const { displayModal, currentImage, currentImageXXL, showDate, thumb, name, title, date, showAddComment } = this.state;

        const { track, showUser = false, isPublic = false } = this.props;
        const image = track.image && track.image.s3SmallLink ? track.image : null;
        const { gpxInfo = {} } = track;
		const { loadIndex = 0 } = gpxInfo;

        const { userStore } = this.props.stores;
        const { user } = userStore;
        const { likes = [], comments = [] } = track;
		const hasLikedNews = track.likes && track.likes.findIndex(e => e.user === user.id) > -1;

        return (
            <div class='w-100 mb-3'>
                <div class='row'>
                    {displayModal && <Modal close={this.closeModal} image={currentImage} imagexxl={currentImageXXL} thumb={thumb} name={name} title={title} date={date} />}

                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                        {/* {JSON.stringify(track, null, 4)} */}

                        <div class={`text-center border rounded-circle imageRounded`} style={image ? `background-image: url("${image.s3SmallLink}");` : ''}>
                            {!image && <i class='fas fa-map-marked text-muted mt-3' style='font-size: 40px;' />}
                        </div>
                        <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                            <a class='stretched-link' href={`/tracks/${track.id}${isPublic ? '/public' : ''}`}>
                                <h5 class='mb-1' style='line-height: 1.0em;'>{track.name}</h5>
                                <div class='text-muted font-weight-lighter'><small>{track.place}</small></div>
                            </a>
                            <div>
                                <small>
                                    {loadIndex > 0 && <span class={`ml-2 text-${mu.getLoadIndexColor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}`}>
                                        <i class='fas fa-hand-rock mr-1' />
                                        {Math.floor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))} -&nbsp;
                                        {mu.getLoadIndexText(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                    </span>}
                                </small>
                            </div>
                            <div>
                                {track.public === 1 && <span class='badge badge-success'>Is public</span>}
                                {showUser && <span class='text-muted font-weight-lighter ml-3'>{mu.displayName(track.users)}</span>}
                            </div>
                            <small class='text-muted'>
                                {track.date && <small onClick={this.toggleDate}>
                                    {showDate ? util.formatDate(track.date, { locale: 'no-NB' }) : util.formatDistance(track.date, new Date(), { locale: 'no-NB' })}<br />
                                </small>}
                            </small>
                            <small class='text-muted'>
                                {track.garminId > 0 && <div class={`float-left mr-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url("./assets/garmin-connect.png");`} />}
                                {track.stravaId > 0&& <div class={`float-left mr-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url("./assets/strava-icon.png");`} />}

                                <i class='fas fa-road' /> <Distance stores={this.props.stores} value={track.distanceKm} />
                                <i class='fas fa-mountain ml-2' /> <Elevation stores={this.props.stores} value={track.elevation} />
                                {track.gpx && <i class='fas fa-map-marked-alt ml-2' />}
                            </small>
                        </div>
                    </div>

                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mt-1' style='padding-left: 122px;'>
                        <div class='row'>
                            {track.images && track.images.map(img => (
                                <div class={`text-center border rounded imageRounded imageRoundedSmall`}
                                    style={img ? `background-image: url("${img.s3SmallLink}");` : ''}
                                    onClick={this.showModal}
                                    data-image={img.s3LargeLink}
                                    data-imagexxl={img.s3XXLargeLink}
                                    data-thumb={track.image && track.image.s3SmallLink}
                                    data-title={track.name}
                                    data-date={track.date}
                                >
                                    &nbsp;
                                </div>
                            ))}
                        </div>
                    </div>

                        <div class='col-10 offset-2 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                            {likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                <i class='fas fa-thumbs-up text-primary' /> {likes.length}
                            </small>}
                            {comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                <i class='fas fa-comment text-primary' /> {comments.length}
                            </small>}
                        </div>

                        <div class='col-10 offset-2 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-3'>
                            <div class='row'>
                                <div class='col-6 text-center'>
                                    <button type='button' class={`btn btn-sm btn-outline-${hasLikedNews ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedNews ? 'text-primary' : ''}`}
                                        onClick={this.likeTrack}
                                        // disabled={hasLikedNews}
                                    >
                                        <i class={`${hasLikedNews ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> Like
                                    </button>
                                </div>
                                <div class='col-6 text-center'>
                                    <button type='button' class='btn btn-sm btn-outline-secondary btn-lg btn-block'
                                        data-id={track.id}
                                        onClick={this.toggleAddComment}
                                    >
                                        <i class='fas fa-comment' /> Comment
                                    </button>
                                </div>
                            </div>
                        </div>

                        {showAddComment && <TrackAddComment
                            stores={this.props.stores}
                            track={track}
                            inputRef={c => this.inputRef[track.id] = c}
                        />}
                        <TrackComments stores={this.props.stores} track={track} col={10} offset={2} compact={true} />

                </div>
            </div>
        );
    }
}

export default Track;
