import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

import mu from '../../lib/musher-util';

@observer
class User extends Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.updateTimer = {};
    }
    
    removeUser = async (e) => {
        let id =  e.target.dataset.id;
        let email =  e.target.dataset.email;
        let team =  e.target.dataset.team;
        if (!id && e.target.parentNode) {
            id =  e.target.parentNode.dataset.id;
            email =  e.target.parentNode.dataset.email;
            team =  e.target.parentNode.dataset.team;
		}
        console.log('id, email, team', id, email, team);
        const { teamStore } = this.props.stores;
        await teamStore.removeMember(team, { id, email });
    }

    adminUpdateUser = (e) => {
        const { email } = e.target.closest('input').dataset;
        const { name } = e.target;
        const field = `${email}-${name}`;

        clearTimeout(this.updateTimer[field]);
        this.updateTimer[field] = setTimeout(() => {
            this.doAdminUpdateUser(e);
        }, 500);
    }

    doAdminUpdateUser = async (e) => {
        const { email } = e.target.closest('input').dataset;
        const { name, value } = e.target;

        // console.log({ email, name, value });
		const { userStore } = this.props.stores;
        await userStore.adminUpdateUser(email, { [name]: value });
    }

    simulateUser = (e) => {
        const { email } = e.target.closest('button').dataset;
        const { userStore } = this.props.stores;
		userStore.getSimulateUser(email);
    }

    render() {
        const { user, team = {} } = this.props;
		const { userStore, teamStore } = this.props.stores;
        const { user: currentUser, isAdmin } = userStore;
        const teams = userStore.findTeams(user.teams);
        const summary = teamStore.findMusherSummary(user.id, team.id) || {};
        const isTeamAdmin = team.user === currentUser.id;
        const image = mu.getImage({ user, team });
        const name = mu.displayName(user);

        // const workoutSummary = dogStore.findWorkoutSummary(dog.id, 2020) || {};
        // {'0':{'year':2020,'dog':16,'count':1,'distanceKm':9.9,'elevation':202,'duration':0,'speedAvg':0}}
        return (
            <div class='w-100 mb-3'>
                <div class='row'>
                    <div class='col-12 d-flex mb-0 position-relative'>
                        {/* {JSON.stringify(dog, null, 4)} */}
                        <div class={`text-center border rounded-circle imageRounded`} style={image ? `background-image: url('${image}'); background-size: cover;` : ''}>
                            {!image && <i class='fas fa-user text-muted mt-3' style='font-size: 40px;' />}
                        </div>
                        <div class='flex-grow-1 pl-3 pr-2'>
                            {/* <span class='float-right'>
                                {dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
                                {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                            </span> */}
                            
                            <h5>{name}</h5>
                            <div>
                                <small class='text-muted'>
                                    <div class='d-flex justify-content-left'>
                                        {user.cellphone && <div class='mr-2'><i class='fas fa-mobile-alt' /> {user.cellphone}</div>}
                                        {user.email && <div class='mr-2'><i class='fas fa-envelope ml-3' /> {user.email}</div>}
                                        {user.url && <div class='mr-2'><a href={mu.asUrl(user.url)} target='_blank' rel='noreferrer'><i class='fas fa-link' /></a></div>}
                                        {user.facebook && <div class='mr-2'><a href={mu.asFacebookLink(user.facebook)} target='_blank' rel='noreferrer'><i class='fab fa-facebook' /></a></div>}
                                        {user.instagram && <div class='mr-2'><a href={mu.asInstagramLink(user.instagram)} target='_blank' rel='noreferrer'><i class='fab fa-instagram' /></a></div>}
                                        {user.snapchat && <div class='mr-2'><a href={mu.asSnapchatLink(user.snapchat)} target='_blank' rel='noreferrer'><i class='fab fa-snapchat' /></a></div>}
                                    </div>
                                </small>
                            </div>
                            {isAdmin && <>
                                <button class='btn btn-block btn-success' onClick={this.simulateUser} data-email={user.email}>
                                    Simulate user
                                </button>

                                <small>
                                    <details>
                                        <summary>settings</summary>
                                        <xmp>{JSON.stringify(user.settings, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>subscription</summary>
                                        <xmp>{JSON.stringify(user.subscription, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>deviceInfo</summary>
                                        <xmp>{JSON.stringify(user.deviceInfo, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>subscription</summary>
                                        <xmp>{JSON.stringify(user.subscription, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>notifications</summary>
                                        <xmp>{JSON.stringify(user.notifications, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>notificationTopics</summary>
                                        <xmp>{JSON.stringify(user.notificationTopics, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>applications</summary>
                                        <xmp>{JSON.stringify(user.applications, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>shadowTags</summary>
                                        <xmp>{JSON.stringify(user.shadowTags, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>garmin</summary>
                                        <xmp>{JSON.stringify(user.garmin, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>strava</summary>
                                        <xmp>{JSON.stringify(user.strava, null, 4)}</xmp>
                                    </details>
                                    <details>
                                        <summary>currentLocationGeocode</summary>
                                        <xmp>{JSON.stringify(user.currentLocationGeocode, null, 4)}</xmp>
                                    </details>
                                    lastActivityEpoch: {util.isoDate(user.lastActivityEpoch)}<br />
                                    activities: {util.format(user.activities)}<br />

                                    {user.settings && <div class='form-check'>
                                        <input class='form-check-input' type='checkbox' value={user.settings.veterinaryAllowed ? 0 : 1} id={`settings.veterinaryAllowed-${user.id}`} name='settings.veterinaryAllowed' checked={user.settings.veterinaryAllowed ? 'checked' : ''} data-email={user.email} onInput={this.adminUpdateUser} />
                                        <label class='form-check-label' for={`settings.veterinaryAllowed-${user.id}`}>
                                            settings.veterinaryAllowed
                                        </label>
                                    </div>}
                                    <div class='form-check'>
                                        <input class='form-check-input' type='checkbox' value={user.twoFactorLogin ? 0 : 1} id={`twoFactorLogin-${user.id}`} name='twoFactorLogin' checked={user.twoFactorLogin ? 'checked' : ''} data-email={user.email} onInput={this.adminUpdateUser} />
                                        <label class='form-check-label' for={`twoFactorLogin-${user.id}`}>
                                            twoFactorLogin
                                        </label>
                                    </div>
                                    <div class='form-check'>
                                        <input class='form-check-input' type='checkbox' value={user.isVeterinary ? 0 : 1} id={`isVeterinary-${user.id}`} name='isVeterinary' checked={user.isVeterinary ? 'checked' : ''} data-email={user.email} onInput={this.adminUpdateUser} />
                                        <label class='form-check-label' for={`isVeterinary-${user.id}`}>
                                            isVeterinary
                                        </label>
                                    </div>
                                    <div class='form-group row'>
                                        <label class='col-sm-2 col-form-label' for={`cellphone-${user.id}`}>cellphone</label>
                                        <div class='col-sm-10'>
                                            <input type='text' id={`cellphone-${user.id}`} name='cellphone' class='form-control' value={user.cellphone} data-email={user.email} onInput={this.adminUpdateUser} />
                                        </div>
                                    </div>
                                    <div class='form-group row'>
                                        <label class='col-sm-2 col-form-label' for={`firstname-${user.id}`}>firstname</label>
                                        <div class='col-sm-10'>
                                            <input type='text' id={`firstname-${user.id}`} name='firstname' class='form-control' value={user.firstname} data-email={user.email} onInput={this.adminUpdateUser} />
                                        </div>
                                    </div>
                                    <div class='form-group row'>
                                        <label class='col-sm-2 col-form-label' for={`lastname-${user.id}`}>lastname</label>
                                        <div class='col-sm-10'>
                                            <input type='text' id={`lastname-${user.id}`} name='lastname' class='form-control' value={user.lastname} data-email={user.email} onInput={this.adminUpdateUser} />
                                        </div>
                                    </div>
                                </small>
                            </>}
                            {user.teams && <>
                                {teams.map(team => (
                                    <span class='badge badge-pill badge-info mr-2'>{team.name}</span>
                                ))}<br />
                            </>}
                            {team.id && summary && <>
                                <small class='text-muted'>
                                    Total:
                                    {summary && <span class='ml-2'><i class='fas fa-road' /> <Distance stores={this.props.stores} value={summary.distanceKm} /></span>}
                                    {summary && <span class='ml-2'><i class='fas fa-mountain' /> <Elevation stores={this.props.stores} value={summary.elevation} /></span>}
                                    {summary && <span class='ml-2'><i class='fas fa-clock' /> {util.secToHms(summary.duration || 0)}</span>}
                                </small><br />
                                <small class='text-muted'>
                                    Avg: 
                                    {summary && <span class='ml-2'><i class='fas fa-road' /> <Distance stores={this.props.stores} value={summary.avgDistance} /></span>}
                                    {summary && <span class='ml-2'><i class='fas fa-mountain' /> <Elevation stores={this.props.stores} value={summary.avgElevation} /></span>}
                                    {summary && <span class='ml-2'><i class='fas fa-clock' /> {util.secToHms(summary.avgDuration || 0)}</span>}
                                    {summary && <span class='ml-2'><i class='fas fa-tachometer-alt' /> <Speed stores={this.props.stores} value={summary.speedAvg} /></span>}
                                    {summary && <span class='ml-2'><i class='fas fa-dog' /> {util.format(summary.avgDogs, 1)}</span>}
                                </small>
                            </>}
                        </div>
                        <div>
                            {isTeamAdmin && user.id !== currentUser.id && <button class='btn btn-sm btn-danger'
                                onClick={this.removeUser}
                                data-email={user.email}
                                data-id={user.id}
                                data-team={team.id}
                            ><i class='fas fa-ban' /></button>}
                            {!isTeamAdmin && user.id === currentUser.id && <button class='btn btn-sm btn-secondary'
                                onClick={this.removeUser}
                                data-email={user.email}
                                data-id={user.id}
                                data-team={team.id}
                            ><i class='fas fa-sign-out-alt' />{team.id}</button>}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default User;
